                //     'Kwun Tong': '',
                //     'North': '',
                //     'Sai Kung': '',
                //     'Sha Tin': '',
                //     'Sham Shui Po': '',
                //     'Southern': '',
                //     'Tai Po': '',
                //     'Tsuen Wan': '',
                //     'Tuen Mun': '',
                //     'Wan Chai': '',
                //     'Wong Tai Sin': '',
                //     'Yau Tsim Mong': '',
                //     'Yuen Long': ''
                // }
            }
          ]
        })
        myChart2.setOption(
